<template id="">
  <div class="row" v-bind:class="{'markiert':this.markiert}">
    <checkbox class="cell font-20pt" v-on:input="$emit('values-change')" v-bind:value="this.row_active"/>

    <div class="cell" v-for="(item, index) in this.items"  :key="item.id" v-on:click.self="mark(item, index)" >
      <a v-bind:class="{'hide':item.edit }" v-on:click="edit(item, index)">{{item.text}}</a>
      <input
        ref="input"
        type="text"
        v-bind:class="{'hide':!item.edit }"
        v-model="item.text"
        v-on:keyup.enter="enter"
        @blur="enter"
        />
    </div>

    <button class="" type="button" name="button"></button>
  </div>
</template>
<script type="text/javascript">
import checkbox from './VueComponents/checkbox.vue'

export default{
  name: 'request',
  components: { checkbox },
  props: ['markiert', 'active', 'items'],
  methods:{
    mark(item, index){
      if(item.text == '')
        this.edit(item, index)
      else
        this.$emit('mark')
    },
    edit(item, index){
      item.edit = !item.noteditable;
      if (!item.noteditable)
        this.edit_index = index;
    },
    enter(){
      this.items.forEach(item => item.edit = false);
      this.$emit('values-change');
    }
  },
  data(){
    return{
      row_active: true,
      edit_index: -1
    }
  },
  updated(){
    this.$nextTick(()=> {
      if(this.edit_index < 0)
        return;

      this.$refs.input[this.edit_index].focus();
    });
  }
}
</script>
<style media="screen" scoped>
.hide{
  display: none;
}
.row{
  display: flex;
  flex-direction: row;
  background-color: white;
}
.row.markiert{
  background-color: rgba(66, 185, 131, 0.5);
}
.cell{
  flex: 1 0 0;
  flex-direction: column;
}
.cell:first-child{
  flex-direction: row;
}
.cell a{
  flex: 1 0 auto;
  line-height: 40pt;
}
.cell input{
  font-size: 20pt;
  flex: 0 0 100%;
  width: 100%;
}
button{
  flex: 0 0 20pt;
}
</style>
